<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm" fullscreen :modal="false">
        <el-tabs v-model="activeName">
            <el-tab-pane label="基础信息" name="first">
                <techo-people ref="vBaseForm" :isAdd="isAdd" :dic="dic" @onBasePeople="handleQueryBasePeople" @onPosition="handlePosition"></techo-people>
            </el-tab-pane>
            <el-tab-pane label="位置信息" name="second">
                <vPeoplePosition ref="vPeoplePosition" :xingzhengqu="dic['行政区划']" :disCodeOption="disCodeOption" :isAdd="isAdd"></vPeoplePosition>
            </el-tab-pane>
            <el-tab-pane label="户籍信息" name="third">
                <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="120px">
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="户号">
                                <el-input v-model="dialogForm.houseNumber" disabled placeholder="请填写户号"></el-input>
                            </el-form-item>
                            <el-form-item label="人户一致标识" prop="householdAgreementSign">
                                <el-select placeholder="请选择人户一致标识" clearable v-model="dialogForm.householdAgreementSign">
                                    <el-option v-for="(item, index) in dic['人户一致']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>  
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="户主姓名">
                                <el-input v-model="dialogForm.householdName" disabled :maxlength="12" placeholder="请填写户主姓名"></el-input>
                            </el-form-item>
                            <el-form-item label="与户主关系" prop="householdRelation">
                                <el-select placeholder="请选择人户一致标识" clearable v-model="dialogForm.householdRelation">
                                    <el-option v-for="(item, index) in dic['户主关系']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item> 
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="户主联系方式">
                                <el-input v-model="dialogForm.householdPhone" :maxlength="12" placeholder="请填写户主联系方式"></el-input>
                            </el-form-item>
                            <el-form-item label="户主身份证号" prop="householdIdCardNumber">
                                <el-input v-model="dialogForm.householdIdCardNumber" :maxlength="18" placeholder="请填写户主身份证号"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
        </el-tabs>
        <div slot="footer" class="dialog-footer">
            <el-button v-if="activeName == 'first'" type="primary" size="small" @click="activeName = 'second'">下一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'first'">上一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'third'">下一步</el-button>
			<el-button v-if="activeName == 'third'" type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button v-if="activeName == 'third'" type="primary" size="small" @click="activeName = 'second'">上一步</el-button>
            <el-button v-if="activeName == 'third'" type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/infact_people_service.js";
import vPeoplePosition from "./components/PeoplePosition.vue";
export default {
  props: {
    disCodeOption: {
      type: Array,
      default: []
    }
  },
  components: {
    vPeoplePosition
  },
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      activeName: "first",
      dic: {
        性别: [],
        民族: [],
        婚姻状况: [],
        政治面貌: [],
        学历: [],
        宗教信仰: [],
        职业类别: [],
        人户一致: [],
        户主关系: [],
        行政区划: []
      },
      dialogForm: {},
      teamRules: {
        householdAgreementSign: [
          { required: true, message: "请选择人户一致标识", trigger: "change" }
        ],
        householdIdCardNumber: [util.validate.isIDCard()],
        householdRelation: [
          { required: true, message: "请选择与户主关系", trigger: "change" }
        ]
      },
      houseInfo: {}
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            if (item.name == "职业类别") {
              that.dic[item.name] = that.handleTreeData(data);
            } else {
              that.dic[item.name] = data;
            }
          });
        }
      });
    });
  },

  watch: {
    activeName(name) {
      if (name == "second") {
        this.$refs["vBaseForm"].baseForm.id &&
          service.requestBaseIdByPeople(
            "people/house/findPage",
            this.$refs["vBaseForm"].baseForm.id,
            data => {
              if (data.totalCount != 0) {
                this.isAdd = false;
                this.dialogForm = data.list[0];
              }
            }
          );
        this.$refs["vBaseForm"].$refs["baseForm"].validate(val => {
          if (!val) {
            this.$nextTick(() => {
              this.activeName = "first";
            });
          }
        });
        this.$refs["vPeoplePosition"].$refs["houseItem"] &&
          this.$refs["vPeoplePosition"].$refs["houseItem"].initGridData();
        this.$refs["vPeoplePosition"].$refs["liveHouseItem"] &&
          this.$refs["vPeoplePosition"].$refs["liveHouseItem"].initGridData();
      } else if (name == "third") {
        new Promise((resolve, reject) => {
          if (this.$refs["vPeoplePosition"].showHujiForm == true) {
            this.$refs["vPeoplePosition"].$refs["houseItem"].$refs[
              "houseItem"
            ].validate(val => {
              if (!val) {
                this.$nextTick(() => {
                  this.activeName = "second";
                });
              }
            });
            this.houseInfo = this.$refs["vPeoplePosition"].$refs[
              "houseItem"
            ].houseInfo;
            resolve();
          } else {
            service.requestHouseInfo(
              this.$refs["vBaseForm"].baseForm.houseId,
              data => {
                this.houseInfo = data;
                resolve();
              }
            );
          }
          if (this.$refs["vPeoplePosition"].showXianzhuForm == true) {
            this.$refs["vPeoplePosition"].$refs["liveHouseItem"].$refs[
              "liveHouseItem"
            ].validate(val => {
              if (!val) {
                this.$nextTick(() => {
                  this.activeName = "second";
                });
              }
            });
          }
        }).then(() => {
          this.dialogForm = {
            ...this.dialogForm,
            householdName: this.houseInfo.householderName,
            houseNumber: this.houseInfo.houseNum,
            householdPhone: this.houseInfo.householderPhone,
            householdAgreementSign: "",
            householdRelation: "",
            householdIdCardNumber: "",
            districtCode: this.houseInfo.districtCode
          };
          this.$refs['dialogForm'].resetFields();
        });
      }
    },
    "dialogForm.householdRelation"(val) {
      if (val == "00") {
        this.dialogForm.householdName = this.$refs["vBaseForm"].baseForm.name;
        this.dialogForm.householdPhone = this.$refs["vBaseForm"].baseForm.phone;
        this.dialogForm.householdIdCardNumber = this.$refs[
          "vBaseForm"
        ].baseForm.idCardNo;
      } else {
        this.dialogForm.householdName = this.houseInfo.householderName;
        this.dialogForm.householdPhone = this.houseInfo.householderPhone;
        this.dialogForm.householdIdCardNumber = "";
      }
    }
  },

  methods: {
    // 职业类型组成树形数据
    handleTreeData(val) {
      // 大类
      let itemONE = _.map(
        _.filter(val, street => {
          return street.code.slice(1, 5) === "0000";
        }),
        street => {
          return {
            code: street.code,
            label: street.name,
            children: []
          };
        }
      );
      // 中类
      _.map(itemONE, item => {
        item.children = _.map(
          _.filter(val, street => {
            return (
              street.code.slice(0, 1) === item.code.slice(0, 1) &&
              street.code.slice(1, 5) !== "0000" &&
              street.code.slice(3, 5) === "00"
            );
          }),
          street => {
            return {
              code: street.code,
              label: street.name,
              children: []
            };
          }
        );
      });
      // 小类
      _.map(itemONE, midItem => {
        _.map(midItem.children, item => {
          item.children = _.map(
            _.filter(val, street => {
              return (
                street.code.slice(0, 3) === item.code.slice(0, 3) &&
                street.code.slice(3, 5) !== "00"
              );
            }),
            street => {
              return {
                code: street.code,
                label: street.name
              };
            }
          );
        });
      });
      return itemONE;
    },
    /** 身份证号选择后回填位置信息 */
    handlePosition(item) {
      this.$refs["vPeoplePosition"].hujidi = item.domicilePlaceDetail;
      this.$refs["vPeoplePosition"].xianzhudi = item.livePlaceDetail;
      this.$refs["vPeoplePosition"].showHujiForm = false;
      this.$refs["vPeoplePosition"].showXianzhuForm = false;
    },
    handleQueryBasePeople(idCardNo, callback) {
      service.requestBasePeopleList(idCardNo, data => {
        callback && callback(data.list);
      });
    },
    /** 保存 */
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let baseInfo = _.cloneDeep(this.$refs["vBaseForm"].baseForm);
          baseInfo.birthDate =
            baseInfo.birthDate &&
            util.time.formatDate(baseInfo.birthDate, "YYYY-MM-DD");
          baseInfo.occupationType =
            baseInfo.occupationType && _.last(baseInfo.occupationType);
          baseInfo.districtCode = this.houseInfo.districtCode;
          if (this.$refs["vPeoplePosition"].showHujiForm == true) {
            let houseAddress = this.$refs["vPeoplePosition"].$refs["houseItem"]
              .positionForm;
            baseInfo.domicilePlace = houseAddress.province; // 户籍地
            baseInfo.domicilePlaceDetail = houseAddress.domicilePlaceDetail; // 户籍地详址
            baseInfo.houseId = houseAddress.houseId; // 户籍地户ID
          }
          if (this.$refs["vPeoplePosition"].showXianzhuForm == true) {
            let liveAddress = this.$refs["vPeoplePosition"].$refs[
              "liveHouseItem"
            ].positionForm;
            baseInfo.livePlace = liveAddress.province; // 居住地
            baseInfo.livePlaceDetail = liveAddress.domicilePlaceDetail; // 居住地详址
            baseInfo.liveHouseId = liveAddress.houseId || ""; // 居住地户ID
          }
          baseInfo.dicMap && delete baseInfo.dicMap;
          if (baseInfo.id) {
            // 修改基本信息
            service.requestUpdatePeopleInfo(baseInfo, data => {
              this.comminForm(data);
            });
          } else {
            // 添加基本信息
            service.requestSavePeopleInfo(baseInfo, data => {
              this.comminForm(data);
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    /** 添加附加信息 */
    comminForm(data) {
      let otherInfo = _.cloneDeep(this.dialogForm);
      // 是否需要修改户信息
      if (
        otherInfo.householdRelation == "00" &&
        otherInfo.householdName != this.houseInfo.householderName
      ) {
        this.houseInfo.householderName = otherInfo.householdName;
        this.houseInfo.householderPhone = otherInfo.householdPhone;
        service.requestUpdateHouse(this.houseInfo, data => {
          console.log("户信息修改了");
        });
      }

      otherInfo.dicMap && delete otherInfo.dicMap;
      if (this.isAdd == true) {
        otherInfo.peopleId = data.id;
        // 添加 户籍人口附加信息
        service.requestSaveHousePeople(otherInfo, data => {
          this.$popup.showNotification({
            type: "success",
            message: "户籍人口添加成功！"
          });
          this.resetForm();
          this.$parent.handleQuery();
        });
      } else {
        // 修改 户籍人口其他信息
        let param = _.pick(otherInfo, [
          "districtCode",
          "houseNumber",
          "householdAgreementSign",
          "householdIdCardNumber",
          "householdName",
          "householdPhone",
          "householdRelation",
          "id",
          "peopleId"
        ]);
        service.requestUpdateHousePeople(param, data => {
          this.$popup.showNotification({
            type: "success",
            message: "户籍人口修改成功！"
          });
          this.resetForm();
          this.$parent.handleQuery();
        });
      }
    },
    /** 取消 */
    resetForm() {
      this.$refs["vBaseForm"].$refs["baseForm"].resetFields();
      this.$refs["vBaseForm"].resetBaseForm();
      this.$refs["vPeoplePosition"].destroyedForm();
      this.$refs["dialogForm"].resetFields();
      this.dialogForm = {};
      this.houseInfo = {};
      this.$data.dialogVisible = false;
      this.activeName = "first";
      this.isAdd = true;
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}
.add-dialog .el-select {
  width: 100%;
}
.add-dialog .el-cascader {
  width: 100%;
}
</style>