<template>
    <el-dialog :custom-class="customClass" class="techo-details-wrapper" :title="title" :visible.sync="visible" :width="width" @open="handleOpenView" @close="handleCloseView"
        :fullscreen="width=='100%'" append-to-body>
        <div class="techo-details__body">
            <el-tabs class="techo-details-tabs" :value="current" type="card">
                <el-tab-pane v-for="(tab, tabIndex) in layout.tabs" :key="tabIndex" :label="tab" :name="tab">
                    <span slot="label"><i style="margin-right:10px;" class="el-icon-date"></i>{{tab}}</span>
                    <div v-if="layout['tab'+tabIndex].type == 'content'" style="height:100%;">
                        <el-row :gutter="10" type="flex" style="height: 55%;">
                            <el-col :span="12" class="left-top">
                                <div class="base-house-people">
                                    <div>
                                    <el-row :gutter="10">
                                        <el-col :span="6" style="text-align: center;">
                                            <img src="./images/photo.jpg" style="height: 120px;width: 110px;">
                                        </el-col>
                                        <el-col :span="9">
                                            <div class="tacho-people-item">
                                                <span class="item-title">姓名：</span>
                                                <span class="item-value">{{ data.name }}</span>
                                            </div>
                                            <div class="tacho-people-item">
                                                <span class="item-title">出生日期：</span>
                                                <span class="item-value">{{ data.birthDate }}</span>
                                            </div>
                                            <div class="tacho-people-item">
                                                <span class="item-title">籍贯：</span>
                                                <span class="item-value">{{ data.dicMap.nativePlace }}</span>
                                            </div>
                                            <div class="tacho-people-item">
                                                <span class="item-title">学历：</span>
                                                <span class="item-value">{{ data.dicMap.education }}</span>
                                            </div>
                                            <div class="tacho-people-item">
                                                <span class="item-title">政治面貌：</span>
                                                <span class="item-value">{{ data.dicMap.politicalStatus }}</span>
                                            </div>
                                        </el-col>
                                        <el-col :span="9">
                                            <div class="tacho-people-item">
                                                <span class="item-title">身份证号：</span>
                                                <span class="item-value">{{ data.idCardNo }}</span>
                                            </div>
                                            <div class="tacho-people-item">
                                                <span class="item-title">性别：</span>
                                                <span class="item-value">{{ data.dicMap.sex }}</span>
                                            </div>
                                            <div class="tacho-people-item">
                                                <span class="item-title">民族：</span>
                                                <span class="item-value">{{ data.dicMap.nation }}</span>
                                            </div>
                                            <div class="tacho-people-item">
                                                <span class="item-title">婚姻状况：</span>
                                                <span class="item-value">{{ data.dicMap.marriage }}</span>
                                            </div>
                                            <div class="tacho-people-item">
                                                <span class="item-title">宗教信仰：</span>
                                                <span class="item-value">{{ data.dicMap.religiousBelief }}</span>
                                            </div>
                                        </el-col>
                                    </el-row>
                                    <el-row :gutter="10">
                                        <el-col :span="24">
                                            <div class="techo-details-tabs row-border">
                                                <el-row v-for="(row,rowIndx) in layout['tab'+tabIndex].tmpl" :key="rowIndx" type="flex" justify="space-around">
                                                    <template v-for="(item,itemIndex) in row">
                                                        <el-col class="column-title" :span="item.col[0]">{{item.title}}</el-col>
                                                        <el-col :span="item.col[1]">{{getData(item.field)}}</el-col>
                                                    </template>
                                                </el-row>
                                            </div>
                                        </el-col>
                                    </el-row>
                                    <el-row :gutter="10">
                                      <el-col :span="24">
                                        <div class="other-info" @click="handleDetailsInfo">更多信息>></div> 
                                      </el-col>
                                    </el-row>
                                    </div>
                                </div>
                            </el-col>
                            <el-col :span="12" class="right-top">
                                <techo-card>
                                    <div slot="header">重点人群</div>
                                    <div id="KeyPeopleStatis" style="height:100%"></div>
                                </techo-card>  
                            </el-col>
                        </el-row>
                        <div class="bottom-family-table">
                            <div class="title">家庭信息</div>
                            <el-table :data="familyData" height="calc(100% - 44px)" border style="width: 100%">
                                <el-table-column type="index" label="序号" width="50" align="center"></el-table-column>
                                <el-table-column prop="name" label="姓名" width="180" align="center"></el-table-column>
                                <el-table-column prop="dicMap.householdRelation" label="与户主关系" align="center"></el-table-column>
                                <el-table-column prop="householdName" label="户主姓名" width="180" align="center"></el-table-column>
                                <el-table-column prop="dicMap.sex" label="性别" align="center"></el-table-column>
                                <el-table-column prop="phone" label="联系方式" align="center"></el-table-column>
                                <el-table-column prop="dicMap.marriage" label="婚姻状况" align="center"></el-table-column>
                                <el-table-column prop="idCardNo" label="身份证号" width="180" align="center"></el-table-column>
                                <el-table-column label="操作" align="center" width="100">
                                    <template slot-scope="scope">
                                        <el-button size="mini" class="techo-list__body-table-button-view" @click="handleDetailsInfo(scope.row)">查看</el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </div>
                    </div>
                    <div v-if="layout['tab'+tabIndex].type == 'map'" class="techo-details-tabs-map">
                        <vBaseMap ref="map"></vBaseMap>
                    </div>
                </el-tab-pane>
            </el-tabs>
        </div>
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'" :title="detailsTitle"></techo-details>
    </el-dialog>
</template>

<script>
import vBaseMap from "@/map/base-map.vue";
import layout from "./layout.js";
import BScroll from "better-scroll";

import config from "./config/config.js";
import service from "../services/infact_people_service.js";

export default {
  components: {
    vBaseMap
  },
  props: {
    // 标题
    title: {
      type: String,
      default: "详情信息"
    },
    // 对话框宽度
    width: {
      type: String,
      default: "50%"
    },
    height: {
      type: String,
      default: "50%"
    },
    // 页面布局配置
    layout: {
      type: Object,
      default() {
        return layout;
      }
    }
  },
  data() {
    return {
      // 组件可见性
      visible: false,
      // 显示详情
      data: {
        dicMap: {
          sex: "",
          nation: "",
          marriage: "",
          politicalStatus: "",
          education: "",
          religiousBelief: "",
          occupationType: ""
        }
      },
      current: layout.tabs[0],
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "housePeople",
      // 重点人群关系图
      KeyPeopleStatisOptions: {},
      KeyPeopleStatisChart: null,
      // 家庭人员表
      familyData: [],
      tableBs: null,

      detailsTitle: ""
    };
  },
  computed: {
    // 返回自定义类，控制对话框高度
    customClass() {
      if (this.width == "100%") {
        return "";
      }

      if (this.height == "50%") {
        return "techo-details-height-50";
      } else if (this.height == "60%") {
        return "techo-details-height-60";
      } else if (this.height == "70%") {
        return "techo-details-height-70";
      } else if (this.height == "80%") {
        return "techo-details-height-80";
      } else if (this.height == "100%") {
        return "techo-details-height-100";
      }
    }
  },
  methods: {
    // 打开视图面板
    handleOpenView() {
      this.$nextTick(() => {
        // 重点人群
        this.KeyPeopleStatisChart = this.$echarts.init(
          this.$el.querySelector("#KeyPeopleStatis")
        );
        this.KeyPeopleStatisChart.on("click", val => {
          if (val.value != undefined) {
            // 获取关注人人员信息
            service.requestFocus(val.value, data => {
              // 重点青少年
              data.iscriminal != undefined &&
                (data.dicMap.iscriminal = data.iscriminal == 1 ? "有" : "无");
              // 校园重点人
              data.isFocus != undefined &&
                (data.dicMap.isFocus = data.isFocus == 1 ? "是" : "否");
              // 流动人口
              data.isfocus != undefined &&
                (data.dicMap.isfocus = data.isfocus == 1 ? "是" : "否");
              // 社区矫正
              data.isrecidivist != undefined &&
                (data.dicMap.isrecidivist =
                  data.isrecidivist == 1 ? "是" : "否");
              data.isgroup != undefined &&
                (data.dicMap.isgroup = data.isgroup == 1 ? "是" : "否");
              data.ishosted != undefined &&
                (data.dicMap.ishosted = data.ishosted == 1 ? "是" : "否");
              data.isdraintube != undefined &&
                (data.dicMap.isdraintube = data.isdraintube == 1 ? "是" : "否");
              data.iscrimeagin != undefined &&
                (data.dicMap.iscrimeagin = data.iscrimeagin == 1 ? "是" : "否");
              // 吸毒人员
              data.isCriminal != undefined &&
                (data.dicMap.isCriminal = data.isCriminal == 1 ? "有" : "无");
              // 精神病
              data.isSecurity != undefined &&
                (data.dicMap.isSecurity = data.isSecurity == 1 ? "是" : "否");
              data.isaccident != undefined &&
                (data.dicMap.isaccident = data.isaccident == 1 ? "有" : "无");
              // 刑满释放
              data.isRecidivism != undefined &&
                (data.dicMap.isRecidivism =
                  data.isRecidivism == 1 ? "是" : "否");
              data.crimeAgain != undefined &&
                (data.dicMap.crimeAgain = data.crimeAgain == 1 ? "是" : "否");
              this.handleDetailsInfo(data, val.value);
            });
          }
        });
        new BScroll(".base-house-people", {
          mouseWheel: true,
          scrollbar: {
            fade: true,
            interactive: false
          }
        });
        this.tableBs =
          this.tableBs ||
          new BScroll(".bottom-family-table .el-table__body-wrapper", {
            mouseWheel: true,
            scrollbar: {
              fade: true,
              interactive: false
            }
          });
      });
      // 获取重点人信息
      this.getFocusPeople();
      // 获取家庭成员
      this.getFamilyPeople();
    },
    // 关闭查看视图面板
    handleCloseView() {
      // 重点人群关系图
      this.KeyPeopleStatisOptions = {};
      this.KeyPeopleStatisChart.dispose();
      this.KeyPeopleStatisChart = null;

      this.familyData = [];
      this.detailsTitle = "";
    },
    // 查询重点人信息
    getFocusPeople() {
      let that = this;
      let keyValue = _.cloneDeep(config.keyValue);
      this.KeyPeopleStatisOptions = _.cloneDeep(config.options);
      service.requestFocusPeople(this.data.peopleId, data => {
        that.KeyPeopleStatisOptions.series[0].data[0].name = that.data.name;
        delete data.basePeopleId;
        delete data.housePeopleId;
        _.map(data, (item, index) => {
          if (item != null) {
            that.KeyPeopleStatisOptions.series[0].data.push({
              name: keyValue[index].name,
              value: {
                focusId: item,
                entity: keyValue[index].entity,
                configKey: keyValue[index].configKey,
                detailsTitle: keyValue[index].name
              },
              draggable: true,
              symbolSize: 60,
              itemStyle: {
                normal: {
                  color: keyValue[index].color
                }
              }
            });
          } else {
            that.KeyPeopleStatisOptions.series[0].data.push({
              name: keyValue[index].name,
              draggable: true,
              symbolSize: 60,
              itemStyle: {
                normal: {
                  color: "#c7c7c7"
                }
              }
            });
          }
          that.KeyPeopleStatisChart.setOption(that.KeyPeopleStatisOptions);
          that.KeyPeopleStatisChart.resize();
        });
      });
    },
    // 查询家庭成员
    getFamilyPeople() {
      // 根据户ID 查询户籍人口 家庭人员信息
      service.requestPageHousePeople(
        {
          districtCode: "",
          name: "",
          houseId: this.data.houseId,
          page: "1",
          pageSize: "1000"
        },
        data => {
          this.familyData = data.list;
        }
      );
    },
    // 查看所有信息
    handleDetailsInfo(data, val) {
      let newData = data.id == undefined ? _.cloneDeep(this.data) : data;
      this.configKey = val == undefined ? "housePeople" : val.configKey;
      this.detailsTitle = val == undefined ? "户籍人口" : val.detailsTitle;
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = newData;
        this.$refs["details"].visible = true;
      });
    },
    // 获取属性信息
    getData(key) {
      return _.get(this.data, key);
    }
  }
};
</script>

<style lang="scss">
@import "./styles/techo-details.scss";
</style>